/* Updated CSS for DetailPage3 */
.detail-page3 {
    display: flex;
    background-color: #1a1a2e;
    color: #fff;
    min-height: 100vh;
}

.sidebar3 { /* Updated class name */
    flex: 0 0 250px;
    background-color: #2c2f36;
    padding: 20px;
    color: #fff;
}

.content3 { /* Updated class name */
    flex-grow: 1;
    padding: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.content3 h1 { /* Updated class name */
    margin-bottom: 20px;
}

.chart-container { /* New class for chart wrapper */
    width: 100%;
    max-width: 800px; /* Adjust width as needed */
    height: 500px; /* Set a fixed height for the chart container */
    margin-top: 20px; /* Add margin for spacing */
}
